.close-btn {
    position: absolute;
    cursor: pointer;
    right: 20px;
    top: 20px;
    width: 20px;
    height: 20px;
}

.close-btn:before, .close-btn:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    height: 20px;
    border: 1px solid black;
}

.close-btn:before {
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
}

.close-btn:after {
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
}
